import React, { Component } from 'react';
import style from '../Component/Login.module.css';
import { NavLink } from 'react-router-dom';

class Login extends Component {
    render() {
        return (
            <div className="container" id={style.container}>
                <h4 id={style.header}>Login Here</h4>
                <div className="row">
                    <form className="col s12">
                        <div className="row">
                            <div className="input-field col s12">
                                <input id="email" type="email" className={style.validate} autoComplete="off"/>
                                <label for="email">Email</label>
                            </div>
                            <div className="input-field col s12">
                                <input id="password" type="password" className={style.validate} autoComplete="off"/>
                                <label for="password">Password</label>
                            </div>
                        </div>
                        <button className="btn btn-success">Login</button><span id={style.register}>Are You New Here ? <NavLink to="/signup">Register Here</NavLink></span>
                    </form>
                </div>
            </div>
        )
    };
};

export default Login;