import React, { Component } from 'react'
import { NavLink } from 'react-router-dom';
import style from '../Component/Navbar.module.css';

class Navbar extends Component {
    render() {
        return (
            <div id={style.container}>
                <nav>
                    <div className="nav-wrapper">
                    <NavLink to="/" className="brand-logo" id={style.brand}>Home</NavLink>
                        <ul id="nav-mobile" className="right hide-on-med-and-down" id={style.menu}>
                            <li><NavLink to="/login"><button className="btn btn-success">Login</button></NavLink></li>
                            <li><NavLink to="/signup"><button className="btn btn-success">Sign Up</button></NavLink></li>
                        </ul>
                    </div>
                </nav>
            </div>
        )
    };
};

export default Navbar;