import React, { Component } from 'react';
import { NavLink } from 'react-router-dom';
import style from '../Component/Home.module.css';

class Home extends Component {
    render() {
        return (
            <div className="container">
                <h1 id={style.heading}>Home Page</h1>
                <div className="card" id={style.card}>
                    <li><NavLink to="/login"><button className="btn btn-success">Login</button></NavLink></li>
                    <li><NavLink to="/signup"><button className="btn btn-success">Sign Up</button></NavLink></li>
                </div>
            </div>
        )
    };
};

export default Home;