import React, { Component } from 'react';
import style from '../Component/SingUp.module.css';
import {NavLink} from 'react-router-dom';

class SignUp extends Component {
    render() {
        return (
            <div className="container" id={style.container}>
                <h4 id={style.header}>Sign Up Here</h4>
                <div className="row">
                    <form className="col s12">
                        <div className="row">
                            <div className="input-field col s12">
                                <input id="first_name" type="text" className="validate" />
                                <label for="first_name">First Name</label>
                            </div>
                            <div className="input-field col s12">
                                <input id="last_name" type="text" className="validate" />
                                <label for="last_name">Last Name</label>
                            </div>
                            <div className="input-field col s12">
                                <input id="email" type="email" className="validate" />
                                <label for="email">Email</label>
                            </div>
                            <div className="input-field col s12">
                                <input id="password" type="password" className="validate" />
                                <label for="password">Password</label>
                            </div>
                            <div className="input-field col s12">
                                <textarea id="comment" className="validate"></textarea>
                                <label for="comment">Comment</label>
                            </div>
                        </div>
                        <button className="btn btn-success">Sign Up</button><span id={style.login}>Have You Already Register ? <NavLink to="/login">Login Here</NavLink></span>
                    </form>
                </div>
            </div>
        )
    };
};

export default SignUp;